body {
  box-sizing: content-box !important; }

*,
*:before,
*:after {
  box-sizing: inherit !important; }

.f-input--outlined .f-input__field {
  color: inherit;
  background-color: white;
  border: 1px solid silver;
  border-radius: 0.25em;
  transition: border-color 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
  will-change: border-color; }
  .f-input--outlined .f-input__field:hover {
    border-color: #6fcf97; }
  .f-input--outlined .f-input__field:focus {
    border-color: #6fcf97;
    box-shadow: inset 0 0 0 calc(2px - 1px) #6fcf97; }
  .f-input--outlined .f-input__field[disabled] {
    cursor: not-allowed;
    border-color: gainsboro; }
  .f-input--outlined .f-input__field:-webkit-autofill, .f-input--outlined .f-input__field:-webkit-autofill:hover {
    -webkit-box-shadow: 0 0 0 10em white inset !important; }
  .f-input--outlined .f-input__field:-webkit-autofill:focus, .f-input--outlined .f-input__field:-webkit-autofill:active {
    -webkit-box-shadow: inset 0 0 0 calc( 2px - 1px) #6fcf97, inset 0 0 0 10em white !important; }

.f-input--outlined.has-error .f-input__field,
.f-input--outlined.has-error .f-input__field:hover,
.f-input--outlined.has-error .f-input__field:focus {
  border-color: #e91e63;
  box-shadow: inset 0 0 0 calc(2px - 1px) #e91e63; }

.f-input--outlined .f-input__label {
  position: absolute;
  display: flex;
  align-items: center;
  pointer-events: none;
  transition: transform 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
  will-change: transform;
  transform-origin: top left;
  top: 0;
  bottom: 0;
  left: calc(1px + 0.9em); }

.f-input--outlined .f-input__field:focus + .f-input__label,
.f-input--outlined .f-input__field.is-filled + .f-input__label {
  transform: translateY(-1em); }

.f-input--outlined .f-input__label-text {
  display: block;
  transform-origin: top left;
  transition: transform 0.2s cubic-bezier(0.51, 0.92, 0.24, 1), color 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
  will-change: transform, color; }
  .f-input--outlined .f-input__label-text:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: -0.35em;
    right: -0.35em;
    z-index: -1;
    background-color: white;
    will-change: transform; }

.f-input--outlined .f-input__field:hover + .f-input__label .f-input__label-text {
  color: #6fcf97; }

.f-input--outlined .f-input__field.is-filled + .f-input__label .f-input__label-text {
  color: inherit; }

.f-input--outlined .f-input__field:focus + .f-input__label .f-input__label-text,
.f-input--outlined .f-input__field.is-filled + .f-input__label .f-input__label-text {
  transform: scale(0.8) translateY(-50%) translateY(-1px);
  color: #6fcf97; }

.f-input--outlined.has-error .f-input__field + .f-input__label .f-input__label-text {
  color: #e91e63; }

.f-input--outlined .f-input__indicator {
  position: absolute;
  transition: color 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
  will-change: color; }
  .f-input--outlined .f-input__indicator--optional {
    top: 1em;
    right: 1em; }
  .f-input--outlined .f-input__indicator--required {
    top: 0.4em;
    right: 0.4em; }

.f-input--outlined .f-input__field:hover ~ .f-input__indicator {
  color: #6fcf97; }

.f-input--outlined .f-input__field:focus ~ .f-input__indicator {
  color: #6fcf97; }

.f-input--outlined .f-input__message {
  margin: 0.5em 0 0 0.5em; }
